/**
 * prism.js Github theme based on GitHub's theme.
 * @author Sam Clarke
 */
code,
 pre{
   color: #333;
   background: none;
   font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
   text-align: left;
   white-space: pre;
   word-spacing: normal;
   word-break: normal;
   word-wrap: normal;
   line-height: 1.4;
 
   -moz-tab-size: 8;
   -o-tab-size: 8;
   tab-size: 8;
 
   -webkit-hyphens: none;
   -ms-hyphens: none;
   hyphens: none;
}
 /* Code blocks */
pre {
   padding: .8em;
   overflow: auto;
   /* border: 1px solid #ddd; */
   border-radius: 3px;
   /* background: #fff; */
   background: #f5f5f5;
}
 /* Inline code */
:not(pre) > code{
   padding: .1em;
   border-radius: .3em;
   white-space: normal;
   background: #f5f5f5;
}
.token.comment,
 .token.blockquote {
   color: #969896;
}
.token.cdata {
   color: #183691;
}
.token.doctype,
 .token.punctuation,
 .token.variable,
 .token.macro.property {
   color: #333;
}
.token.operator,
 .token.important,
 .token.keyword,
 .token.rule,
 .token.builtin {
   color: #a71d5d;
}
.token.string,
 .token.url,
 .token.regex,
 .token.attr-value {
   color: #183691;
}
.token.property,
 .token.number,
 .token.boolean,
 .token.entity,
 .token.atrule,
 .token.constant,
 .token.symbol,
 .token.command,
 .token.code {
   color: #0086b3;
}
.token.tag,
 .token.selector,
 .token.prolog {
   color: #63a35c;
}
.token.function,
 .token.namespace,
 .token.pseudo-element,
 .token.class,
 .token.class-name,
 .token.pseudo-class,
 .token.id,
 .token.url-reference .token.variable,
 .token.attr-name {
   color: #795da3;
}
.token.entity {
   cursor: help;
}
.token.title,
 .token.title .token.punctuation {
   font-weight: bold;
   color: #1d3e81;
}
.token.list {
   color: #ed6a43;
}
.token.inserted {
   background-color: #eaffea;
   color: #55a532;
}
.token.deleted {
   background-color: #ffecec;
   color: #bd2c00;
}
.token.bold {
   font-weight: bold;
}
.token.italic {
   font-style: italic;
}
 /* JSON */
.language-json .token.property {
   color: #183691;
}
.language-markup .token.tag .token.punctuation {
   color: #333;
}
 /* CSS */
code.language-css,
 .language-css .token.function {
   color: #0086b3;
}
 /* YAML */
.language-yaml .token.atrule {
   color: #63a35c;
}
code.language-yaml {
   color: #183691;
}
 /* Ruby */
.language-ruby .token.function {
   color: #333;
}
 /* Markdown */
.language-markdown .token.url {
   color: #795da3;
}
 /* Makefile */
.language-makefile .token.symbol {
   color: #795da3;
}
.language-makefile .token.variable {
   color: #183691;
}
.language-makefile .token.builtin {
   color: #0086b3;
}
 /* Bash */
.language-bash .token.keyword {
   color: #0086b3;
}
 /* highlight */
pre[data-line] {
   position: relative;
   padding: 1em 0 1em 3em;
}
pre[data-line] .line-highlight-wrapper {
   position: absolute;
   top: 0;
   left: 0;
   background-color: transparent;
   display: block;
   width: 100%;
}
pre[data-line] .line-highlight {
   position: absolute;
   left: 0;
   right: 0;
   padding: inherit 0;
   margin-top: 1em;
   background: hsla(24, 20%, 50%,.08);
   background: -webkit-gradient(linear, left top, right top, color-stop(70%, hsla(24, 20%, 50%,.1)), to(hsla(24, 20%, 50%,0)));
   background: linear-gradient(to right, hsla(24, 20%, 50%,.1) 70%, hsla(24, 20%, 50%,0));
   pointer-events: none;
   line-height: inherit;
   white-space: pre;
}
pre[data-line] .line-highlight:before, 
 pre[data-line] .line-highlight[data-end]:after {
   content: attr(data-start);
   position: absolute;
   top: .4em;
   left: .6em;
   min-width: 1em;
   padding: 0 .5em;
   background-color: hsla(24, 20%, 50%,.4);
   color: hsl(24, 20%, 95%);
   font: bold 65%/1.5 sans-serif;
   text-align: center;
   vertical-align: .3em;
   border-radius: 999px;
   text-shadow: none;
   -webkit-box-shadow: 0 1px white;
           box-shadow: 0 1px white;
}
pre[data-line] .line-highlight[data-end]:after {
   content: attr(data-end);
   top: auto;
   bottom: .4em;
}
